/*============================ 
// Global styles
=============================*/
* {
  margin: 0;
  padding: 0;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
}

body {
  scroll-behavior: smooth;
  margin: 0;
  padding: 0;
  font-family: var(--font-body);
  font-size: 1rem;
}

ul,
li {
  padding: 0;
  margin: 0;
  list-style: none;
  line-height: 0;
}

a {
  display: inline-block;
  transition: all 0.4s ease-in-out;
  text-decoration: none;
}
a:link, a:visited, a:focus {
  text-decoration: none;
  outline: 0;
}
a:hover {
  text-decoration: none;
}

img {
  max-width: 100%;
  height: auto;
}

input:focus,
textarea:focus,
select:focus {
  outline: 0;
  box-shadow: none;
}

.no-padding {
  padding-left: 0 !important;
  padding-right: 0 !important;
}
.no-padding__left {
  padding-left: 0 !important;
}
.no-padding__right {
  padding-right: 0 !important;
}

section,
.section {
  position: relative;
}

@keyframes toBottomFromTop {
  49% {
    transform: translateY(-100%);
  }
  50% {
    opacity: 0;
    transform: translateY(100%);
  }
  51% {
    opacity: 1;
  }
}
/*===========================
//	Utility CSS.
===========================*/
.section-padding {
  padding: 120px 0;
}
@media (max-width: 991px) {
  .section-padding {
    padding: 90px 0;
  }
}
@media (max-width: 767px) {
  .section-padding {
    padding: 70px 0;
  }
}

.section-padding--two {
  padding: 130px 0;
}
@media (max-width: 767px) {
  .section-padding--two {
    padding: 100px 0;
  }
}

.section-padding--3 {
  padding: 100px 0;
}
@media (max-width: 767px) {
  .section-padding--3 {
    padding: 70px 0;
  }
}

/*================================
// para-text/normal text style
=================================*/
.circle-radius {
  border-radius: 50%;
}

.sticky-tab-style {
  position: sticky;
  top: 77px;
  z-index: 1;
  background: white;
  padding: 15px 25px;
  box-shadow: rgba(149, 157, 165, 0.2) 0px 8px 24px;
  border-radius: 0px 0px 8px 8px;
  transition: all 0.4s ease-in-out;
}

/*======================================
// pre-loader
=====================================*/
#preloader {
  background: url("../preloader.gif") rgb(255, 255, 255) no-repeat scroll center center;
  height: 100%;
  left: 0;
  overflow: visible;
  position: fixed;
  top: 0;
  width: 100%;
  z-index: 9999999;
}

.animate__animated {
  opacity: 0;
  visibility: hidden;
}

.swiper-vertical {
  max-height: 205px;
}

.marquee {
  width: 100%;
  overflow: hidden;
}

/* ========================== */
/* bootstrap accordion reset
/* =========================== */
.accordion-button {
  background: #F8F8F8;
  font-family: var(--font-outfit);
  font-weight: 500;
  font-size: 1.125rem;
  line-height: 23px;
  color: var(--color-title);
  height: 60px;
  padding: 1rem 25px;
}
@media (max-width: 768px) {
  .accordion-button {
    font-size: 16px;
  }
}

.accordion-button::after {
  background-image: url("../images/icons/right-arrow.svg");
  background-size: unset;
}

.accordion-button:not(.collapsed)::after {
  background-image: url("../images/icons/white-up-arrow.svg");
  transform: rotate(0deg);
  height: 10px;
}

.accordion-flush .accordion-item .accordion-button:not(.collapsed) {
  color: var(--color-white);
  background-color: var(--color-primary);
  box-shadow: none;
  border-radius: 5px 5px 0px 0px;
}

.accordion-button:focus {
  outline: none;
  box-shadow: none;
}

.accordion-flush .accordion-item {
  position: relative;
  margin-bottom: 7px;
  border-radius: 5px;
  border: none;
}
.accordion-flush .accordion-item .accordion-button {
  border-radius: 5px;
}
.services-details-page .accordion-item {
  margin-bottom: 15px;
}

.accordion-body {
  border: 1px solid #DCDBE5;
  padding: 0;
  border-radius: 0px 0px 5px 5px;
}

/* End Custom accordion */
/* ========================== */
/* =========================== */
.nice-select {
  background: transparent;
  border: none;
  font-family: var(--font-outfit);
  font-weight: 400;
  font-size: 15px;
  color: var(--color-gray12);
  padding-right: 100px;
  text-transform: capitalize;
}
.nice-select .list {
  width: 100%;
}

.nice-select:after {
  border-bottom: 2px solid #8A8A8A;
  border-right: 2px solid #8A8A8A;
  height: 10px;
  width: 10px;
  right: -5px;
  top: 44%;
}

